.mainBlock {
    height: 100vh;
    background-color: #252525;
}


.text {
    text-transform: uppercase !important;
    color: #ffb400;
    font-weight: 400;
    margin: 18px 0 10px;
    position: relative;
}

.text h1 {
    font-size: 52px;
    line-height: 22px;
}

.text h2 span {
    color: #fff;
    display: block;
}

.photo {
    width: 300px;
    height: 400px;
    background-color: #ff8a8a;
}




